#wrapper {
  background-color: #D3D3D3;
}

#header {
  background-color: #337ab7;
  padding: 0 5px;
  border-bottom: 1px solid #2e6da4;}
  #header h1 {
    color: white;
    margin: 0;
  }

#content {
  background-color: #D3D3D3;
  padding: 20px;
  min-height: 100vh;
}

.suggestions {
  background-color: white;
  list-style-type: none;
  border-radius: 3px;
  box-shadow: 0px 0px 15px grey;}
  .suggestions li {
    padding: 1px;
    cursor: pointer;
    background-color: white;
  }
  .suggestions li:hover {
    background-color: #e5e5e5;
  }

.journey {
  background-color: white;
  padding: 5px;
  margin: 15px 0;}
  .journey .journeyTitle {
    border-bottom: 1px solid lightgrey;}
  .journey .journeyOverview {
    border-bottom: 1px solid lightgrey;}
    .journey .journeyOverview div {
      display: inline-block;
    }
  .journey .journeyLeg {
    padding: 5px;}
    .journey .journeyLeg div {
      display: inline-block;
      width: 30%;}
    .journey .journeyLegArrow {
      text-align: center;
    }

